* {
  box-sizing: border-box;
  outline: none; }

html {
  scroll-behavior: smooth;
  height: 100%; }

body {
  margin: 0 auto;
  padding: 0;
  font-family: "montserrat", "Arial", sans-serif; }

.container {
  max-width: 1350px;
  padding: 0 30px;
  margin: 0 auto; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

.swiper-pagination {
  text-align: left;
  z-index: 11; }

.swiper-pagination-bullet {
  width: 15px;
  height: 15px;
  background: #ffffff;
  transition: 0.3s all ease-in-out;
  opacity: 0.4; }

.swiper-pagination-bullet-active {
  width: 24px;
  height: 24px;
  opacity: 1;
  background-color: rgba(255, 255, 255, 0.4);
  transform: rotate(45deg);
  animation: bullet 10.5s linear; }

.swiper-container-horizontal > .swiper-pagination-bullets, .swiper-pagination-custom, .swiper-pagination-fraction {
  bottom: 60px;
  left: 75px;
  width: 100%; }

@keyframes bullet {
  0% {
    border-top: 12px solid rgba(255, 255, 255, 0.4);
    border-left: 12px solid rgba(255, 255, 255, 0.4);
    border-right: 12px solid rgba(255, 255, 255, 0.4);
    border-bottom: 12px solid rgba(255, 255, 255, 0.4); }
  25% {
    border-top: 12px solid #ffffff;
    border-left: 12px solid rgba(255, 255, 255, 0.4);
    border-right: 12px solid rgba(255, 255, 255, 0.4);
    border-bottom: 12px solid rgba(255, 255, 255, 0.4); }
  50% {
    border-top: 12px solid #ffffff;
    border-right: 12px solid #ffffff;
    border-left: 12px solid rgba(255, 255, 255, 0.4);
    border-bottom: 12px solid rgba(255, 255, 255, 0.4); }
  75% {
    border-top: 12px solid #ffffff;
    border-right: 12px solid #ffffff;
    border-bottom: 12px solid #ffffff;
    border-left: 12px solid rgba(255, 255, 255, 0.4); }
  100% {
    border-top: 12px solid #ffffff;
    border-left: 12px solid #ffffff;
    border-right: 12px solid #ffffff;
    border-bottom: 12px solid #ffffff; } }

.feedback {
  background-color: #272222;
  color: #ffffff; }

.feedback__content {
  padding: 9px 0;
  display: flex;
  justify-content: flex-end; }
  .feedback__content a {
    color: #ffffff;
    text-decoration: none;
    font-weight: 600;
    font-size: 12px;
    line-height: 140%; }

.feedback__question {
  margin-right: 24px; }

.slider {
  display: flex;
  align-items: flex-end;
  padding: 65px 0 120px;
  position: relative; }

.slider__big {
  width: 65.47%;
  margin-right: 30px; }

.slider__wrapper {
  width: 34.53%;
  height: 826px;
  position: relative;
  display: flex;
  align-items: flex-end; }

.slider__small {
  width: 100%; }

.slider__item--small {
  height: 593px; }

.slider__label {
  position: absolute;
  width: 385px;
  height: 203px;
  background-color: #ce3730;
  z-index: 10;
  top: 0;
  left: 0;
  padding: 30px;
  color: #ffffff; }
  .slider__label h3 {
    font-weight: bold;
    font-size: 26px;
    line-height: 140%; }
  .slider__label p {
    padding: 0 100px 0 0;
    margin: 0;
    font-size: 14px;
    line-height: 140%; }
  .slider__label a {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 58px;
    height: 58px;
    border-radius: 50%;
    background-color: #ffffff;
    position: absolute;
    bottom: 38px;
    right: 35px;
    transition: 0.3s all ease-in-out; }
    .slider__label a:hover, .slider__label a:focus {
      opacity: 0.7;
      transform: scale(1.2); }
  .slider__label svg {
    stroke: #ce3730; }

.slider__offer {
  position: absolute;
  width: 475px;
  height: 247px;
  z-index: 10;
  bottom: 50px;
  right: 60px; }

.slide {
  width: 100%;
  height: 826px;
  position: relative; }

.slider__item--small {
  height: 593px; }

.slide__picture {
  position: absolute;
  width: 100%;
  height: 100%;
  bottom: 0;
  left: 50%;
  transform: translate(-50%, 0%); }

.offer {
  padding: 35px;
  background-color: #ffffff; }
  .offer img {
    margin-bottom: 69px; }
  .offer p {
    padding: 0 100px 0 0;
    margin: 0;
    font-size: 24px;
    line-height: 140%; }
  .offer a {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 58px;
    height: 58px;
    border-radius: 50%;
    background-color: #ce3730;
    position: absolute;
    bottom: 38px;
    right: 35px;
    transition: 0.3s all ease-in-out; }
    .offer a:hover, .offer a:focus {
      opacity: 0.7;
      transform: scale(1.2); }
  .offer svg {
    stroke: #ffffff; }
